@import url(http://fonts.googleapis.com/css?family=Open+Sans:300italic,400italic,600italic,700italic,800italic,400,600,700,300,800);
@import './components/fonts.scss';

$wikaOrange: #ef8122;
$wikaDarkOrange: #f57e20;
$wikaBlack: #001429;
$wikaDarkBlue: #171a3f;
$wikaGrey: #9b9b9b;


body {
  background: #fff;
  font-family: 'roboto', sans-serif;
  color: #4e4e4e;
  line-height: 22px;
  margin: 0;
}

h1, h2, h3, h4, h5 {
  font-family: 'MyriadPro';
  color: $wikaDarkBlue;
  &.bold, .bold {
    font-family: 'Helvetica-Bold';
  }
}

p {
  font-family: 'MyriadPro';
  color: $wikaDarkBlue;
}

a {
  font-family: 'MyriadPro';
}

#FooterMenu {
  background-color: $wikaGrey;
  padding: 2rem 0;
  ul {
    list-style-type: none;
    li {
      padding: 4px 0;
    }
  }
  a {
    text-decoration: none;
  }
  a, p {
    color: #fff;
  }
}

.header-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 9999;
  padding-top: 1rem;
  padding-bottom: 1rem;

  &.on-scroll {
    background-color: rgba(255, 255, 255, 0.8);
  }
}

.menu-logo {
  max-width: 240px;
}

.menu-list-container {
  display: flex;

  // flex: 1;
  .menu-level {
    &-1 {
      padding: 1rem;

      a {
        display: block;
        text-decoration: none;
        color: $wikaBlack;

        &.active, &:hover {
          color: $wikaOrange;
          transition: ease 0.3s ;
        }
      }
    }
  }
}

.slick {
  &-prev {
    left: 25px;
    z-index: 2;
  }
  &-next {
    right: 25px;
    z-index: 2;
  }
}

.banner-items {
  position: relative;
  // background-repeat: no-repeat;
  // background-position: center;
  // background-size: cover;
  // padding-bottom: 700px;
  &__images {
    max-height: 700px;
  }
  &__text-container {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    align-items: center;
    padding-left: 2rem;
    padding-right: 2rem;
  }
  &__title {
    display: block;
    color: #fff;
    font-size: 40px;
    margin-bottom: 2rem;
  }
  &__desc {
    color: #fff;
    font-size: 25px;
  }
}

.section {
  &-container {
    padding-top: 2rem;
  }
  &-title {
    font-family: 'MyriadPro';
    font-size: 40px;
    margin-bottom: 1rem;
    &.bold, .bold {
      font-family: 'Helvetica-Bold';
    }
  }
}

.about {
  &-desc {
    font-size: 18px;
    text-align: justify;
    .highlight {
      background-color: $wikaDarkOrange;
      padding: 2px 8px;
      border-radius: 100px;
      font-style: italic;
      font-weight: bold;
    }
  }
  &-image-container {
    margin-right: 2rem;
  }
}

.product-images {
  filter: grayscale(1);
  &:hover {
    filter: grayscale(0);
    transition: ease-in .2s;
  }
}

.media {
  &-images {
    margin-bottom: 1rem;
  }
  &-desc {
    text-align: justify;
  }
}